
Template.__checkName("browseCameras");
Template["browseCameras"] = new Template("Template.browseCameras", (function() {
  var view = this;
  return HTML.DIV({
    "class": "page list-group"
  }, HTML.Raw('\n        <span class="list-group-item active">\n            Registered Cameras\n        </span>\n        '), Blaze.Each(function() {
    return Spacebars.call(view.lookup("availableCameras"));
  }, function() {
    return [ "\n            ", HTML.A({
      href: function() {
        return Spacebars.mustache(view.lookup("pathFor"), "cameraDetail");
      },
      "class": "list-group-item"
    }, Blaze.View("lookup:name", function() {
      return Spacebars.mustache(view.lookup("name"));
    })), "\n        " ];
  }), HTML.Raw('\n        <a href="/cameraDetail" class="list-group-item">Work camera</a>\n    '));
}));
