
Template.__checkName("errors");
Template["errors"] = new Template("Template.errors", (function() {
  var view = this;
  return HTML.DIV({
    "class": "errors"
  }, "\n    ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("errors"));
  }, function() {
    return [ "\n      ", Spacebars.include(view.lookupTemplate("error")), "\n    " ];
  }), "\n  ");
}));

Template.__checkName("error");
Template["error"] = new Template("Template.error", (function() {
  var view = this;
  return HTML.DIV({
    "class": "alert alert-danger",
    role: "alert"
  }, HTML.Raw('\n    <button type="button" class="close" data-dismiss="alert">&times;</button>\n    '), Blaze.View("lookup:message", function() {
    return Spacebars.mustache(view.lookup("message"));
  }), "\n  ");
}));
