
Template.__checkName("layout");
Template["layout"] = new Template("Template.layout", (function() {
  var view = this;
  return HTML.DIV({
    "class": "container"
  }, "\n    ", Spacebars.include(view.lookupTemplate("header")), "\n    ", Spacebars.include(view.lookupTemplate("errors")), "\n    ", HTML.DIV({
    id: "main"
  }, "\n      ", Spacebars.include(view.lookupTemplate("yield")), "\n    "), "\n  ");
}));
